













import Vue from 'vue';
import { mapGetters, mapMutations } from 'vuex';

export default Vue.extend({
	computed: {
		...mapGetters(['isLogin']),
	},

	methods: {
		...mapMutations(['clearUserData']),

		logout() {
			this.clearUserData();
			this.$store.commit('openSnackbar', '로그아웃 되었습니다! 안녕히가세요!');
			this.$router.replace('/');
		},
	},
});
