import Vue from 'vue';
import Vuex, { Store } from 'vuex';

import { UserInfo } from '@/types/index';
import { setUserInfo, getUserInfo, removeUserInfo } from '@/utils/localStorage';
import { fetchAllPosts } from '../api/posts';
import { formatDatetime } from '../utils/datetime';

Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		username: '로그인이 필요합니다.',
		avatar: '',
		email: '',
		token: '',
		snackbar: { state: false, message: '' },
		posts: [],
	},

	getters: {
		isLogin(state): boolean {
			return state.token !== '';
		},
	},

	mutations: {
		setUserData(state, userData) {
			state.username = userData.username;
			state.avatar = userData.avatar;
			state.email = userData.email;
			state.token = userData.token;
			setUserInfo(userData);
		},

		clearUserData(state) {
			state.username = '로그인이 필요합니다.';
			state.avatar = '';
			state.email = '';
			state.token = '';
			removeUserInfo();
		},

		openSnackbar(state, message: string) {
			state.snackbar.message = message;
			state.snackbar.state = true;
		},

		closeSnackbar(state) {
			state.snackbar.state = false;
		},

		setPosts(state, posts) {
			state.posts = posts;
		},
	},
	actions: {
		async FETCH_POSTS({ commit }) {
			try {
				const { data } = await fetchAllPosts();
				if (data)
					commit(
						'setPosts',
						data.map(post => {
							post.created_time = formatDatetime(post.created_time);
							return post;
						}),
					);
			} catch (error) {
				throw new Error(error);
			}
		},
	},
	modules: {},
});
