import { $http } from './index';
import { Post } from '@/types';
import { AxiosPromise } from 'axios';

export const fetchAllPosts = (): AxiosPromise => {
	return $http.get('/article');
};

export const createPost = (post: Post): AxiosPromise => {
	return $http.post('/article', post);
};

export const editPost = (post): AxiosPromise => {
	return $http.put('/article', post);
};

export const viewPost = (id): AxiosPromise => {
	return $http.put(`/article/${id}`);
};

export const removePost = (id): AxiosPromise => {
	return $http.delete(`/article/${id}`);
};

export const fetchComments = (id): AxiosPromise => {
	return $http.get(`/comment/${id}`);
};

export const createComment = (comment): AxiosPromise => {
	return $http.post('/comment', comment);
};

export const editComment = (comment): AxiosPromise => {
	return $http.put('/comment', comment);
};

export const removeComment = (id): AxiosPromise => {
	return $http.delete(`/comment/${id}`);
};
